@import "../assets/mixins";

.wd-info-panel {
  background: white;
  box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  z-index: 2;
  min-height: 100vh;

  &__inner {
    width: 100%;
    max-width: 1600px;
    margin: 0 auto auto auto;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    @include font-regular;
    font-size: 1rem;
    cursor: default;

    @media (orientation: landscape) {
      padding: 5vh;
    }
    @media screen and (orientation: portrait) {
      padding: 5vw;
    }
  }

  &__block {
    &-title {
      font-size: 1.2rem;
    }

    &-text {
      margin-top: 0.5rem;
      line-height: 1.5rem;

      &._left-right {
        display: flex;

        @media (orientation: landscape) {
          flex-direction: row;
        }
        @media screen and (orientation: portrait) {
          flex-direction: column;
        }
      }

      &-left {
        @media (orientation: landscape) {
          width: 55%;
        }

        @media screen and (orientation: portrait) {
          width: 100%;
        }
      }

      &-right {
        min-height: 460px;

        @media (orientation: landscape) {
          width: 45%;
          margin-left: 2rem;
        }

        @media screen and (orientation: portrait) {
          width: 100%;
          height: 460px;
          margin-top: 2rem;
        }
      }
    }

    & + & {
      margin-top: 2rem;
    }
  }
}
